body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0; }

#root {
  width: 100%;
  height: 100%; }

.App {
  text-align: center;
  background-color: #282c34;
  color: #ccc;
  width: 100%; }

@font-face {
  font-family: 'editionregular';
  src: url(/haas-app/static/media/edition-webfont.a49014a9.woff2) format("woff2"), url(/haas-app/static/media/edition-webfont.8a51a27e.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'mermaidbold';
  src: url(/haas-app/static/media/mermaid-webfont.d5b66168.woff2) format("woff2"), url(/haas-app/static/media/mermaid-webfont.a358db8d.woff) format("woff");
  font-weight: normal;
  font-style: normal; }

.App-link {
  color: #DAA1A6; }

header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10;
  padding: 0 0px 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box; }

.logo {
  position: relative;
  text-transform: uppercase;
  width: 60px;
  height: 60px;
  cursor: pointer;
  transition: .3s;
  font-family: 'mermaidbold';
  color: #ccc; }
  .logo:hover {
    background-color: #1D2026;
    transition: .3s; }
  .logo .logo-text {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }

.menu_icon {
  position: relative;
  width: 60px;
  height: 60px;
  cursor: pointer; }
  .menu_icon:hover {
    background-color: #1D2026;
    transition: .3s; }
  .menu_icon .line_container {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%); }
  .menu_icon .line {
    width: 20px;
    height: 1px;
    background-color: #ccc; }
  .menu_icon .line_1 {
    margin-bottom: 8px; }

.menu {
  color: #ccc;
  position: absolute;
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  text-align: left;
  box-sizing: border-box;
  pointer-events: none; }
  .menu.open {
    pointer-events: auto; }
    .menu.open .menu_bar {
      opacity: 1 !important; }
  .menu .menu_container {
    position: relative;
    padding: 60px 30px;
    box-sizing: border-box; }
  .menu .menu_bar {
    width: 60px;
    height: 60px;
    background-color: #1D2026;
    position: absolute;
    top: 0;
    right: 0; }
  .menu .menu_content {
    position: relative; }
  .menu ul {
    padding: 0;
    margin: 0; }
    .menu ul li {
      list-style-type: none;
      overflow: hidden; }
      .menu ul li.menu_link {
        font-size: 170px;
        font-family: 'editionregular'; }
  .menu .menu_link_container {
    position: relative;
    overflow: hidden;
    display: inline-block;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    opacity: 0;
    cursor: pointer;
    transition: color .3s; }
    .menu .menu_link_container.life:hover {
      color: #0DFFD5; }
    .menu .menu_link_container.life:before {
      background-color: #0DFFD5; }
    .menu .menu_link_container:before {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 2px;
      top: 50%;
      background-color: #DAA1A6;
      -webkit-transform: translate(-100%, -50%);
              transform: translate(-100%, -50%); }
    .menu .menu_link_container:hover {
      color: #DAA1A6;
      transition: color .3s;
      transition-delay: .1s; }
      .menu .menu_link_container:hover:before {
        -webkit-transform: translate(100%, -50%);
                transform: translate(100%, -50%);
        transition: -webkit-transform 0.4s cubic-bezier(0.86, 0, 0.07, 1);
        transition: transform 0.4s cubic-bezier(0.86, 0, 0.07, 1);
        transition: transform 0.4s cubic-bezier(0.86, 0, 0.07, 1), -webkit-transform 0.4s cubic-bezier(0.86, 0, 0.07, 1); }

.heroSection {
  background-size: cover;
  width: 100%; }
  .heroSection p {
    font-family: 'Abel'; }

.bgtext {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: 'editionregular';
  font-size: 50vh;
  color: #1D2026;
  pointer-events: none; }
  @media screen and (max-width: 767px) {
    .bgtext {
      display: none; } }

.grid_container {
  margin: 0 auto;
  position: relative;
  padding: 100px 0;
  overflow: auto;
  box-sizing: border-box;
  min-height: 100vh; }

.grid_row {
  width: 100%; }
  .grid_row.hidden {
    display: none; }

.grid_item {
  border: 1px solid rgba(255, 255, 255, 0.9);
  height: 300px;
  display: inline-block; }

